// Tinymce editor
form .tox-tinymce {
  border: 1px solid #ced4da !important;
}

form .tox :not(svg):not(rect) {
  color: #495057;
}

.tox .tox-toolbar,
.tox .tox-toolbar__overflow,
.tox .tox-toolbar__primary {
  // background: var(--bs-light) !important;
  background-attachment: local;
  background-color: #fff !important;
  background-image: repeating-linear-gradient(#e3e3e3 0 1px, transparent 1px 39px);
  background-position: center top 40px;
  background-repeat: no-repeat;
  background-size: calc(100% - 11px * 2) calc(100% - 41px);
  display: flex;
  flex: 0 0 auto;
  flex-shrink: 0;
  flex-wrap: wrap;
  padding: 0 0;
  transform: perspective(1px);
}

.tox .tox-editor-container {
  background-color: #fff !important;
}

.tox .tox-statusbar {
  align-items: center;
  background-color: #fff !important;
  border-top: 1px solid #e3e3e3;
  color: rgba(34, 47, 62, .7) !important;
  display: flex;
  flex: 0 0 auto;
  font-size: 14px;
  font-weight: 400;
  height: 25px;
  overflow: hidden;
  padding: 0 8px;
  position: relative;
  text-transform: none;
}

.tox .tox-button:hover {
  background-color: #fff5cc !important;
  border-color: #fff5cc !important;
}

